import { Component, OnInit, ViewEncapsulation, forwardRef, Input, Output, EventEmitter, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { LocaleService } from '@farris/ui-locale';

interface Tag {
  value?:string;
  name:string;
  tagType?:string;
  color?:string;
  closable?:boolean;
  checked?:boolean;
  size?:string;
  checkable?:boolean;
}

@Component({
  selector: 'farris-tags',
  templateUrl: './tags.component.html',
  // styleUrls: ['./tags.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TagsComponent),
      multi: true
    }
  ]
})
export class TagsComponent implements OnInit {

  _data:string;
  @Input()
  set data(val) {
    this._data = val;
    setTimeout(() => {
      this.tagInit(this._data);
    });
  }
  get data() {
    return this._data;
  }
  @Input() value:string;
  //æ¯å¦æ¯æ
  _checkable:boolean;
  @Input()
  set checkable(val) {
    this._checkable = val;
    this.cd.detectChanges();
  }
  get checkable() {
    return this._checkable;
  }
  //æ¯å¦æ¯ææ·»å 
  _addable:boolean = true;
  @Input()
  set addable(val) {
    this._addable = val;
    this.addTagAble = val;
  }
  get addable() {
    return this._addable;
  }
  //ç±»å
  _tagType:string;
  @Input()
  set tagType(val) {
    this._tagType = val;
    this.cd.detectChanges();
  }
  get tagType() {
    return this._tagType;
  }
//èªå®ä¹é¢è²
  _color:string;
  @Input()
  set color(val) {
    this._color = val;
    this.styleInit();
  }
  get color() {
    return this._color;
  }

  //æ¯å¦æ¯æå³é­
  _closable:boolean = true;
  @Input()
  set closable(val) {
    this._closable = val;
  }
  get closable() {
    return this._closable;
  }

  _readonly:boolean = false;
  @Input()
  set readonly(val) {
    this._readonly = val;
    if(!this._readonly){
      this._closable = true;
      // this._addable = true;
      this.addTagAble = true;
    }
    else{
      this.activeTag = '';
      this._closable = false;
      // this._addable = false;
      this.addTagAble = false;
    }
    this.cd.detectChanges();
  }
  get readonly() {
    return this._readonly;
  }

  @Input()
  className:string;
  @Output()
  checkChanged = new EventEmitter<any>();
  @Output()
  tagClick = new EventEmitter<any>();
  @Output()
  dataChanged = new EventEmitter<any>();
  @Output()
  closed = new EventEmitter<any>();

  @ViewChild('addInputText') addInputText: ElementRef;

  addInput:boolean = false;
  addTagValue:string = '';
  placeholder:string;
  checkedTags:Array<any> = [];
  innerTags: Tag[] = [];
  tagStyle;
  activeTag:string;
  //æ·»å æé®æ¯å¦ç¦ç¨
  addTagAble:boolean = true;
  private onChangeCallback: Function = () => { };
  private onTouchedCallback: Function = () => { };

  constructor(public localeService: LocaleService,private cd: ChangeDetectorRef) { }

  ngOnInit() {
    if(this._checkable){
      this._closable = false;
    }
    this.placeholder = this.localeService.getValue('tag.placeholder');
  }
  tagInit(data){
    this.innerTags = [];
    if(!data) return;
    let tagsArray = data.split(',');
    const tagsData = tagsArray.filter(item=>{
      if(item !== ''){
        return item;
      }
    })
    if(tagsData && tagsData.length){
      for(let i = 0; i< tagsData.length; i++){
        let tagdata = {
          name:''
        };
        tagdata.name = tagsData[i];
        this.innerTags.push(tagdata);
      }
    }
    else{
      this.innerTags = [];
    }
  }
  /**
   * ç¹å»å³é­
   * @param e event
   * @param tag tag
   * @param index tagç´¢å¼
   */
  closeHandler(e: any, tag, index:number) {
    e.preventDefault();
    this.innerTags.splice(index,1);
    const tagdata = this.tagProcess(this.innerTags);
    this.onChangeCallback(tagdata);
    this.onTouchedCallback();
    this.closed.emit(tag.name);
    this.dataChanged.emit(tagdata);
  }
  /**
   * ç¹å»tag
   * @param e event
   * @param tag
   */
  clickHandler(e, tag){
    e.preventDefault();
    if(this._readonly) return;
    if(this.activeTag != tag['name']){
      this.activeTag = tag.name;
    }
    this.tagClick.emit(tag.name);
    if(!this._checkable) return;
    if(tag.hasOwnProperty('checked')){
      tag.checked = !tag.checked;
    }
    else{
      tag.checked = true;
    }
    this.checkedTags = this.innerTags.filter((data)=>{
      return data.checked;
    });
    const tagdata = this.tagProcess(this.innerTags);
    this.onChangeCallback(tagdata);
    this.onTouchedCallback();

    const CheckChangedData = {
      data: tagdata,
      tag: tag.name,
      checked: this.tagProcess(this.checkedTags)
    };
    this.checkChanged.emit(CheckChangedData);
  }
  /**
   * ç¹å»æ·»å tagæé®
   */
  addHander(){
    if(!this._addable || !this.addTagAble) return;
    this.addInput = true;
    this.addTagValue = '';
    setTimeout(() => {
      this.addInputText.nativeElement.focus();
    });
  }
  /**
   * æ·»å tagå¤±å»ç¦ç¹
   */
  addInputBlur(event){
    if(!this._addable || !this.addInput) return;
    if(this.addTagValue){
      const hastag = this.innerTags.find(tag => {
        return tag.name == this.addTagValue;
      });
      if(hastag){
        this.addInput = false;
        return;
      }
      else{
        const tag = {name: this.addTagValue};
        this.innerTags.push(tag);
        const tagdata = this.tagProcess(this.innerTags);
        this.dataChanged.emit(this.tagProcess(this.innerTags));
        this.onChangeCallback(tagdata);
        this.onTouchedCallback();
      }
    }
    this.addInput = false;
  }

  writeValue(val:string): void {
    this._data = val;
    this.tagInit(this._data);
  }

  registerOnChange(fn: any): void {
    this.onChangeCallback = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouchedCallback = fn;
  }

  tagProcess(data) {
    if(data && data.length){
      let tagProcessData = [];
      data.forEach(tag => {
        tagProcessData.push(tag.name);
      });
      return tagProcessData.join(',');
    }
    else {
      return '';
    }
  }
 //æ¹åéæåº¦
  hexToRgba(hex, opacity = 1) {
    const reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
    var sColor = hex.toLowerCase();
    if(sColor && reg.test(sColor)){
        if(sColor.length === 4){
            var sColorNew = "#";
                for(var i=1; i<4; i+=1){
                    sColorNew += sColor.slice(i,i+1).concat(sColor.slice(i,i+1));       
                }
                sColor = sColorNew;
        }
        //å¤çå­ä½çé¢è²å¼
        var sColorChange = [];
        for(var i=1; i<7; i+=2){
            sColorChange.push(parseInt("0x"+sColor.slice(i,i+2)));       
        }
        return "RGB(" + sColorChange.join(",") + ',' + opacity + ")";
    }else{
        return sColor;       
    }}
  styleInit() {
    if(this.color){
      this.tagStyle = {
        color: this._color,
        background: this._color? this.hexToRgba(this._color,0.1):'',
        borderColor: this._color
      };
    }
    else{
      this.tagStyle = '';
    }
    this.cd.detectChanges();
  }
}
